*{
    box-sizing: border-box;
    font-family: sans-serif;
}
body{
    margin: 0;
    padding: 0;
}
/********Navbar**********/
.navbar{
    background-color: black;
    height:50px;
    color: white;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 40px;
}
.navbar > .nav-brand{
    flex-grow: 1;
    margin-left : 20px;
    font-size: 1.7rem;
}
.navbar >  a:not(.navbrand){
    margin:0 20px
}
.navbar > a {
    text-decoration: none;
    color: white;
}
.navbar > a:hover{
    color: rgba(225,225,225,1)
}
/********Blog-decoration*******/
.blog-container {
    width: 90%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}
.blog-container > div.blog-preview {
    width:80%;
    max-width: 600px;
    border : 1px solid rgba(0,0,0,0.1);
    border-radius : 15px;
    padding:0 20px;
    margin-bottom : 30px;
}
/* Create-blog*/
.create-blog-container{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.input-container{
   display: flex;
   flex-direction: column;
   width: 60%;
   max-width: 500px;
   
}
.input-container > textarea{
    resize: none;
    margin-bottom: 30px;
    border:none;
    border: 2px solid black;
    border-radius: 6px;
}
.input-container > input {
   height:30px;
   margin-bottom: 30px;
   border:none;
   border-bottom: 2px solid black;
}
.create-blog-container > h1{
    margin-bottom: 50px;
}
.input-container > label {
    font-size: 1.3rem;
    margin-bottom: 8px;
}
input:focus{
    outline: none;
}
.btn {
    border:none;
    height: 1.8rem;
    border-radius:7px;
    cursor:pointer;
}
/*delete button*/
.delete-container{
    display: flex;
    
}
.delete-container > a , h2 , h1 {
    flex-grow: 1;
}
.container-from-Delete{
    display: flex;
    align-items: center;
    justify-content: center;
}
.container-from-Delete > button {
    border:none;
    background-color: black;
    color: white;
    border-radius : 5px;
}
a {
    
    color: black;
}
/* detail container */
.upper-container-for-details{
    display: flex;
    align-items: center;
    justify-content: center;
}
.detail-container{
    width:55%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    
}